import { AfterViewInit, Component } from '@angular/core';
import { interval } from 'rxjs';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements AfterViewInit {
  title = 'jakob-birthday';

  ciggarets= 0;
  beers= 0;
  hours= 0;
  bugs= 0;

  constructor(){

  }

  ngAfterViewInit(){

    let intervalId = setInterval(() => {
      this.beers = this.beers + 1;
      if(this.beers === 15) clearInterval(intervalId)
  }, 100)

  let intervalIdx = setInterval(() => {
    this.ciggarets = this.ciggarets + 1;
    if(this.ciggarets === 21) clearInterval(intervalIdx)
}, 100)

let intervalIds = setInterval(() => {
  this.hours = this.hours + 1;
  if(this.hours === 32) clearInterval(intervalIds)
}, 100)

  }

}
